.list {
  margin: 0.5rem 1rem;
  display: grid;
  grid-template-columns: repeat(3, minmax(250px, 1fr));
  grid-gap: 0.5rem;
  box-sizing: border-box;
}
.listItem {
  background-color: rgb(255, 179, 17);
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  border: 4px solid rgb(204, 132, 15);
  border-radius: 5px;
  display: flex;
  align-items: center;
}
.content {
  flex-grow: 1;
  display: flex;
  align-items: center;
}
.checkbox {
  padding-right: 1rem;
  font-size: 1.5rem;
  cursor: pointer;
}
.label,
.rename,
.noItem {
  font-family: "Courier New", Courier, monospace;
  font-size: 2rem;
  font-weight: bold;
  color: white;
  text-wrap: wrap;
}
.label {
  flex-grow: 1;
  padding: 0 1rem;
  cursor: text;
  border-left: 1px rgb(204, 132, 15) solid;
  border-right: 1px rgb(204, 132, 15) solid;
  /* white-space: wrap;
    word-wrap: break-word;
    overflow-wrap: break-word;
    overflow:hidden; */
}
.rename {
  flex-grow: 1;
  color: black;
  padding: 0 1rem;
  border: 0;
  border-radius: 5px;
  outline: none;
  display: flex;
  text-wrap: wrap;
  width: 100%;
  max-width: 100%;
  min-width: 50%;
  line-height: 1rem;
  height: 100%;
  box-sizing: border-box;
}
.noItem {
  margin: 1rem 0;
  grid-column: 1 / span 2;
}
.trash-div {
  display: flex;
  justify-content: right;
  padding-left: 1rem;
}
.trash {
  font-size: 1.5rem;
  cursor: pointer;
}
