.add {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
}
.add-box {
  flex-grow: 1;
  height: 2rem;
  background-color: rgb(255, 179, 17);
  border: 5px solid rgb(204, 132, 15);
  border-right: 0;
  border-radius: 5px 0 0 5px;
  padding: 0.3rem 0.5rem;
  color: whitesmoke;
  font-family: "Courier New", Courier, monospace;
  font-size: 2rem;
  font-weight: bold;
  cursor: pointer;
}
.add-box:focus {
  cursor: auto;
  outline: none;
}
.add-box::placeholder {
  color: whitesmoke;
  font-size: 1.5rem;
  font-weight: lighter;
}
.add-button,
.check-button {
  height: 2rem;
  width: 2rem;
  box-sizing: content-box;
  padding: 0.3rem;
  background-color: rgb(255, 179, 17);
  border: 5px solid rgb(204, 132, 15);
  border-left: 0;
  border-radius: 0 5px 5px 0;
  color: whitesmoke;
  font-size: large;
  font-weight: bold;
  cursor: pointer;
}
.add-button {
  border-right: 0;
  border-radius: 0;
}
